<template>
	<div id="content-wrapper">
    <div id="content" v-show="!noData">
      <div class="title">
        <h3>{{infoDetail.title}}</h3>
      </div>
      <div class="context">
        <div v-html="infoDetail.content"></div>
      </div>
      <div class="dianzan">
        <div class="dianzan-title">
          <span class="title-row">
            —————
          </span>
          <span class="title-text">
            以上方案是否解决了您的问题？
          </span>
          <span class="title-row">
            —————
          </span>
        </div>
        <div class="dianzan-content">
          <div>
            <img class="img-responsive" @click="dianzhan(true)" v-if="!yesShow" src="../assets/images/yes.png">
            <img class="img-responsive" @click="dianzhan(false)" v-if="yesShow" src="../assets/images/yes_red.png">
            <span>有用</span>
          </div>
          <div>
            <img class="img-responsive" @click="noDianzhan(true)" v-if="!noShow" src="../assets/images/no.png">
            <img class="img-responsive" @click="noDianzhan(false)" v-if="noShow" src="../assets/images/no_red.png">
            <span>没用</span>
          </div>
        </div>
      </div>
    </div>
    <div class="no-data" v-show="noData">
      对不起，没有查询到相应的问题！
    </div>
    <div class="open" v-if="xsdzqOpen">
      <div class="content" @click="openApp">
        <span>打开APP 查看更多</span>
      </div>
    </div>
    <div class="help">
      <span>没有找到想问的问题？</span>
      <a @click="gotoService">
        <img class="img-responsive service"  src="../assets/images/helpService.png">
        <span>问问在线客服</span> 
      </a>
      <a href="tel:95399">
        <img class="img-responsive phone" src="../assets/images/helpPhone.png">
        <span>拨打客服电话</span> 
      </a>
    </div>
    <mt-popup v-model="popupVisible" popup-transition="popup-fade" position="top">
      <div class="open-wrap">
         <img class="img-responsive" src="../assets/images/open.png">
      </div>
    </mt-popup>
  </div>
</div>
</template>

<script>
  import config from "../config.js"
  import util from "../utils/commonUtils"
  import jsApi from "../utils/jsApi"
  import { Toast,Popup } from 'mint-ui';
  export default {
    name: 'Content',
    data () {
      return {
        infoDetail:{
          title:""
        },
        xsdzqOpen:false,
        popupVisible:false,
        yesShow:false,
        noShow:false,
        noData:false
      }
    },
    created:function(){  
      let titleObject={
        back:true,
        share:true,
        title:"问题解答",
        shareTitle:"帮助中心 帮助中心",
        description:"帮助中心 帮助中心 description description description"
      };
      this.$emit("routerChange",titleObject);
      setTimeout(function(){ 
        window.scrollTo(0,0);
      }, 1);
      let params =this.$route.params;
      if(util.isEmptyObject(params)){
        this.$router.push({name:"helpMain"});
      }else{
        let index=params.id;
        this.getInfo(index);
      }
      if(!util.browser().isXsdzq){
        this.xsdzqOpen=true;

      }
    },
    methods:{
      getInfo:function(index){
        let utilUrl = config.getInfo;
        let url = utilUrl+"?id="+index;
        let that = this;
        this.$http.get(url).then(function(response){
          let data=response.body;
          if(data.resCode==0){
            that.infoDetail=data.result;
          }else{
            that.noData=true;
          }
        });
      },
      gotoService:function(){
        var url="https://tb.53kf.com/webCompany.php?arg=9007838&style=1&header=none&";//device=ios
        if(util.browser().isIOS){
          url=url+"device=ios";
        }else if(util.browser().isAndroid){
          url=url+"device=android";
        }
        console.log("url: "+url);
        var title = "新时代证券欢迎你-客服咨询";
        var description = "";
        var shareTitle = "";
        var right = "";
        if(util.browser().isXsdzq){
          jsApi.newView(url,title,description,shareTitle,right);
        }else{
          window.location.href = url + "?rnd=" + new Date().getTime();
        }
      },
      dianzhan:function(flag){
        this.yesShow=flag;
        this.noShow=false;
        Toast("谢谢你的反馈");
      },
      noDianzhan:function(flag){
        this.noShow=flag;
        this.yesShow=false;
        Toast("谢谢你的反馈");
      },
      openApp:function(){
        if(util.browser().isWX){
          this.popupVisible=true;
        }else{
          console.log("aaaa");

        }

      }
    }
  }
</script>

<style>

.no-data{
  margin: 10px;
}
#content,#content-wrapper {
  min-height: 100%;
  overflow: hidden;
  background: #fff;
}
#content table{
  border: 1 !important;
  max-width: 100% !important;
}
#content table tr td{
  /*border-width: 1px;
  border-style: solid;*/
}
#content .title h3{
  font-size: 24px;
  font-weight: bold;
}
#content .title {
  padding: 12px 18px;
  margin: 0;
  font-size: 18px;
  color: #000000;
  /*border-bottom:  thin solid #DCDCDC;*/
}
#content .context{
  margin: 12px 18px;
}
#content img{
  display: block;
  max-width: 100%;
  height: auto;
}
#content .dianzan{
  margin-top: 30px;
  margin-bottom: 90px;
}

#content .dianzan-title{
  font-size: 14px;
  color: #8D8D8D;
  text-align: center;
}
#content .dianzan-title .title-row{
  color: #DCDCDC;
}
#content .dianzan-title .title-text{
  margin: 0 10px;
}
#content .dianzan-content{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #8D8D8D;
  margin: 20px 60px;
}
#content .dianzan-content >div{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#content .dianzan-content img{
  width: 35px;
  margin-bottom: 6px;
}
#app .mint-popup{
  background: none;
}
.open-wrap{
  width: 135%;
  padding-top: 25px;
}
.open{
  position: fixed;
  z-index: 3;
  background: #fff;
  bottom: 50px;
  left: 0;
  right: 0;
  display: flex;
  color: #8D8D8D;
  justify-content: center;
  font-size: 11px;
  text-align: center;
}
.open .content{
  background: #C4011D;
  border-radius: 24.5px;
  width: 90%;
  height: 40px;
  line-height: 40px;
  font-size: 17px;
  color: #FFFFFF;
}
.help{
  position: fixed;
  z-index: 2;
  background: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  color: #8D8D8D;
  justify-content: center;
  font-size: 11px;
  padding: 17px 10px 17px 10px;
  text-align: center;
  border-top: thin solid #dcdcdc;
}
.help span{
  color: #8D8D8D;
}
.help a{
  display: flex;
}
.help .phone{
  width:12px;
  height:15px;
  margin-right: 5px;
  margin-left: 10px;
}
.help .service{
  width:17px;
  height:16px;
  margin-right: 5px;
  margin-left: 10px;
}

</style>


