
function getViewSize() {
	return {
		"w": window['innerWidth'] || document.body.clientWidth,
		"h": window['innerHeight'] || document.body.clientHeight
	}
}

function getPixcs(){
	return {
		w:screen.width,
		h:screen.height
	}
}

function getFullSize(){
	var widch = Math.max(document.documentElement.clientWidth, document.body.clientWidth) + Math.max(document.documentElement.scrollLeft, document.body.scrollLeft);
	var height = Math.max(document.documentElement.clientHeight, document.body.clientHeight) + Math.max(document.documentElement.scrollTop, document.body.scrollTop);
	widch = Math.max(document.documentElement.scrollWidth, widch);
	height = Math.max(document.documentElement.scrollHeight, height);
	return {
		"widch": widch,
		"height": height
	};
}

function isEmptyObject(e) {  
	var t;  
	for (t in e)  
		return !1;  
	return !0  
}

function removeAllSpace(str) {
	return str.replace(/\s+/g, "");
}


function browser(){
	var userAgent = navigator.userAgent;
	console.log(userAgent);
	var webview={};
	webview.isXsdzq=(function(){
		return  userAgent.indexOf('xsdzq') > 0 || userAgent.indexOf('XSDZQ') > 0;
	})();
	webview.isChrome=(function(){
		return userAgent.indexOf('Chrome') > 0 || userAgent.indexOf('CriOS') > 0;
	})();
	webview.isIOS=(function(){
		return /(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent);
	})();
	webview.isAndroid=(function(){
		return userAgent.indexOf('Android') > -1 || userAgent.indexOf('Adr') > -1; //android终端
	})();
	var lower=userAgent.toLowerCase();
	webview.isWX=(function(){
		return /micromessenger/.test(lower);
	})();

	

	return webview;
}


export default{
	getViewSize,
	getFullSize,
	getPixcs,
	isEmptyObject,
	removeAllSpace,
	browser
}


