var util={};
var YYAppReady=false;
util.init=function(){
	document.addEventListener('hsAppReady', function(){
		YYAppReady=true;
	}, false);
	console.log("init");
	console.log(YYAppReady);
}
util.callNative=function(param){
    if(window.winner){
        var params=JSON.stringify(param);
        winner.hsJsFunction(params);
    }else{
        console.log("--------------++"+YYAppReady);
        if(YYAppReady){
            hsJsFunction(param);
            console.log("--------------++");
        }else{
            document.addEventListener("hsAppReady", function() {
                YYAppReady = true,
                hsJsFunction(param);
            }, false)
        }
    }
}
util.finish=function(){
    var param={
        provider:{
            providerName: "xsdzq"
        },                                 
        funType:   "forward",             
        funName:   "finish" 
    };
    this.callNative(param);
}
util.share=function(url,title,description){
    var param={
        provider:{
            providerName:"xsdzq"
        },                                
        funType:"share",          
        funName:"WXshare",   
        url:url,   
        title:title,      
        description:description,
        right:"share"
    };
    this.callNative(param);
}
util.newView=function(url,title,description,shareTitle,right){
   var param={
        provider:{
            providerName: "xsdzq"
        },                                 
        funType:   "forward" ,             
        funName:   "webview" , 
        action: "new",//retain :webview 保持不变。new：新打开一个webveiw。老的webview也保留。（先实现new的逻辑）
        url: url,      
        left: "back", //导航栏的左边。一般都是back。也有home的需要。先默认back
        title:title,//导航栏中间。内容标题
        right:right,//导航栏右边。空或者取不到不做操作。share分享图标。find 查询的放大镜的图标。
        describe:description,
        shareTitle:shareTitle
    };
    this.callNative(param);
}

export default util


