<template>
	<div id="list">
    <div class="no-result" v-if="noResult">
      <span>暂时没有相应类型的问题</span>
    </div>
    <mt-loadmore  :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore" >
      <ul class="questions">
        <li v-bind:key="index" @click="gotoDetail(index)"  v-for="(info,index) in infoList">
          <div>
            <span>{{info.title}}</span>
          </div>
          <span><i class="right"></i></span>
        </li>
      </ul>
      <div v-if="more" class="more" @click="moreGetInfos()">
        <span>查看更多</span>
      </div>
      <div v-if="allLoaded" class="no-more">
        <span>没有更多了!</span>
      </div>
    </mt-loadmore>
    <div>
      <div class="help">
        <span>没有找到想问的问题？</span>
        <a @click="gotoService">
          <img class="img-responsive service"  src="../assets/images/helpService.png">
          <span>问问在线客服</span> 
        </a>
        <a href="tel:95399">
          <img class="img-responsive phone" src="../assets/images/helpPhone.png">
          <span>拨打客服电话</span> 
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  import config from "../config.js"
  import util from "../utils/commonUtils"
  import { Indicator, Loadmore } from 'mint-ui';
  export default {
    name: 'List',
    data () {
      return {
        id:null,
        more:false,
        search:null,
        selected:0,
        categoryList:null,
        infoList:null,
        categoryIndex:0,
        allLoaded:false,
        pageNumber:0,
        noResult:false,
        pageSize:15,
        infoList:[],
        titleObject:{
          back:true,
          title:"帮助中心",
          search:true
        }
      }
    },
    created:function(){
      let params =this.$route.params;
      if(util.isEmptyObject(params)){
        this.$router.push({name:"main"});
      }else{
        this.id=params.id;
        this.initCategory();
        this.titleObject.title=params.title;
      }
      this.$emit("routerChange",this.titleObject);
    },
    methods:{
      initCategory:function(id){
        this.getInfos(0);
      },
      gotoSearch:function(){
        this.$router.push({name:"search"});
      },
      getInfos:function(pageNumber){
        Indicator.open();
        let utilUrl = config.getInfosByCategoryId;
        //重置参数
        this.infoList=[];
        this.pageNumber=0;
        this.allLoaded = false;
        this.noResult=false;
        let url = utilUrl+"?"+"id="+this.id+"&pageNumber="+this.pageNumber+"&pageSize="+this.pageSize;
        console.log(url);
        let that = this;
        this.$http.get(url).then(function(response){
          Indicator.close();
          let data=response.body;
          if(data.resCode==0){
            that.infoList=data.result;
            if(that.infoList.length>=data.pagination.totalItems&&that.infoList.length!=0&&data.pagination.totalItems>15){
              that.allLoaded = true;
            }else{
              that.allLoaded = false;
            }
            if(data.result.length==0){
              that.noResult=true;
            }else{
              that.noResult=false;
            }
            //判断还有更多
            if(that.infoList.length<data.pagination.totalItems&&data.pagination.totalItems>15){
              that.more=true;
            }else{
              that.more=false;
            }
          }
        });
      },
      getMore:function(){

      },
      moreGetInfos:function(){
        Indicator.open();
        let utilUrl = config.getInfosByCategoryId;
        let number = this.pageNumber+1;
        let url = utilUrl+"?"+"id="+this.id+"&pageNumber="+number+"&pageSize="+this.pageSize;
        let that = this;
        this.$http.get(url).then(function(response){
          Indicator.close();
          let data=response.body;
          if(data.resCode==0){
            for(let i =0; i< data.result.length;i++){
              console.log(data.result[i]);
              that.infoList.push(data.result[i]);
            }
            if(that.infoList.length>=data.pagination.totalItems){
              that.allLoaded = true;
              that.more = false;
            }else{
              that.more = true;
              that.allLoaded = false;
            }
          }
          that.$refs.loadmore.onBottomLoaded();
        });
      },
      gotoDetail:function(index){
        console.log(index);
        let info =this.infoList[index];
        console.log(info);
        this.$router.push({name:"content",params:{id:info.id}});
      },
      imageLoad:function($event){
        if($event.target){
          $event.target.parentNode.parentNode.firstChild.style.height=$event.target.height+"px";
        }
      },
      gotoService:function(){
        var url ="http://www17.53kf.com/m.php?cid=70867336&arg=9007838&style=1";
        window.location.href = url + "?rnd=" + new Date().getTime();
      },
      loadTop:function(){
        console.log("loadTop");
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        console.log("loadBottom");
        this.pageNumber=this.pageNumber+1;
        this.moreGetInfos(this.selected,this.pageNumber);
      }
    }
  }
</script>

<style scope>
#list{
  background: #fff;
  min-height:100%;
}
#list .questions{
  font-size: 14px;
}
#list .no-result{
  font-size: 14px;
  padding: 15px 10px;
  text-align: center;
}

#list li {
  display: block;
  margin: 0 0 0 16px;
  padding: 13px 0;
  border-bottom: thin solid #f1f1f1;
}
#list .mintui.mintui-search{
  font-size: 20px;
}
#list .nav{
  display: flex;
  flex-flow: nowrap;
}
#list .mint-cell{
  margin-left: 20px;
  border-bottom: thin solid #dcdcdc;
}
#list .mint-cell-wrapper {
  padding-left: 0;
  padding-right: 30px;
}
#list li {
  position: relative;
}
#list li >div{
  padding-right: 35px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

#list li i:after {
  border: solid 2px #c8c8cd;
  border-bottom-width: 0;
  border-left-width: 0;
  content: " ";
  top: 51%;
  right: 20px;
  position: absolute;
  width: 10px;
  height: 10px;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
}
#list .mint-loadmore{
  margin-bottom: 60px;
}
#list .title{
  margin-top: 10px;
  margin-bottom: 10px;
}
#list .help-nav{
  margin-top: 20px;
  margin-bottom: 20px;
}

#list .title .title-lead-wrap{
  padding-top: 5px;
  padding-bottom: 5px;
}
#list .title .title-lead{
  color: #000000;
  font-size: 1.6rem;
}
#list .allSortWrap.col-xs-12{
  padding-left: 0;
  padding-right: 0;
}
.ul-container{
  margin-bottom: 50px;
}
.searchbar-inner{
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  background-color: #F5F5F5;
  border-radius: 15px;
  padding: 5px 10px;
}
.searchbar-inner .mintui-search{
  font-size: 1.4rem;
  color: #d9d9d9;
}
.searchbar-inner .mintui-search-text{
  width: 80%;
  font-size: 1.2rem;
  color: #515151;
  padding-top: 2px;
  text-align: center;
}
.allSort{

}

.sortMenu{
  min-width: 100%; 
  background-color:#fff; 
  overflow-x: scroll; 
  -webkit-overflow-x: scroll;
  border-bottom: thin solid #dcdcdc;
}
.sortMenu::-webkit-scrollbar{ 
  width: 0; 
  height: 0;   
  background-color: #fff;  
}
.sortMenu-ul { 
  min-width:600px; 
  float: left;
  text-align: center;
  font-size: 13px;
  word-break: keep-all;
  padding-top: 13px;
  padding-bottom: 12px;
  color:#000000;
}
.sortMenu .cell{ 
  display: inline-block; 
  font-size: 1.4rem;
  margin: 0px 0.6rem;
  height: 22px;
  line-height:22px;
}
.sortMenu .cell.special a{
  color: #ff474c;
}
.sortMenu .cell.special:before {
  content: '';
  height: 2px;
  width: 100%;
  background: #ff474c;
  position: absolute;
  bottom: 0px;
}
.sortMenu .all{
  right: 0;
  top: 0;
  height: 30px;
  width: 30px;
  position: absolute;
  background: #fff;
  z-index: 10;
  display: flex;
  justify-content:center;
  align-items:center;
}
.sortMenu .all:before{
  content: '';
  height: 25px;
  width: 1px;
  position: absolute;
  box-shadow: 1px 0px 1px #e0e0e0;
  left: 0px;
}
#list a:hover, a:focus{
  text-decoration: none;
}
#list .allSort, a{
  color: #000;
}
#list  a.is-selected{
  color: #C4011D;
  border: thin solid;
  border-radius: 100px;
  padding: 8px 15px;
  margin-bottom: 0;
}
#list .mint-tab-item{
  padding: 8px 15px;
}
#list .mint-navbar > a:first-child{
  margin-left: 10px;
}
#list .mint-navbar > a:last-child{
  margin-right: 10px;
}
#list .mint-cell-wrapper{
  font-size:14px;
  color:#000000;
}
#list .mint-cell-wrapper .mint-cell-title{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#list .mint-navbar .mint-tab-item {
  margin-right: 5px;
}

#list .mint-tab-item-label{
  padding-top: 1px;
  padding-bottom: 1px;
}
#list .help{
  position: fixed;
  z-index: 2;
  background: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  color: #8D8D8D;
  justify-content: center;
  font-size: 11px;
  padding: 17px 10px 17px 10px;
  text-align: center;
  border-top: thin solid #dcdcdc;
}
#list .help span{
  color: #8D8D8D;
}
#list .help a{
  display: flex;
}
#list .help .phone{
  width:12px;
  height:15px;
  color: #8D8D8D;
  margin-right: 5px;
  margin-left: 10px;
}
#list .more{
  text-align: center;
  font-size: 14px;
  color: #4169E1;
  margin: 10px;
}
#list .help .service{
  width:17px;
  height:16px;
  color: #8D8D8D;
  margin-right: 5px;
  margin-left: 10px;
}
#list .no-more{
  font-size: 14px;
  height: 44px;
  line-height: 44px;
  text-align: center;
}
</style>


